import * as React from "react";

interface IAttributesBox {
    icon: string;
    leftSideHeader: string;
    leftSideBody: string;
    rightSideHeader: string;
    rightSideBody: string;
}

export class AttributesBox extends React.Component <IAttributesBox, {}> {
    render() {
        return <li>
            <div className="attributes-image">
                <img className="img-circle img-responsive" src={this.props.icon} alt="" />
            </div>
            {this.makeAttributeBox("Tour", "attributes-heading", this.props.leftSideHeader, this.props.leftSideBody)}
            {this.makeAttributeBox("Steps", "attributes-panel-inverted", this.props.rightSideHeader, this.props.rightSideBody)}
        </li>
    }

    private makeAttributeBox(type: string, className: string, heading: string, body: string): JSX.Element {
        return (
            <div className={className}>
                <div className="attributes-heading">
                    <h4>{type}</h4>
                    <h4 className="subheading">{heading}</h4>
                </div>
                <div className="attributes-body">
                    <p className="params-text">{body}</p>
                </div>
            </div>
        );
    }
}


